import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { VisitorService } from 'src/app/api/controllers/Visitor';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { MatDrawer } from '@angular/material/sidenav';

import * as _ from 'underscore';
import * as moment from 'moment';
import _moment from "moment-timezone";

@Component({
    selector: 'app-visitor-reports-data',
    templateUrl: './visitor-reports-data.component.html',
    styleUrls: ['./visitor-reports-data.component.scss']
})
export class VisitorReportsDataComponent implements OnInit {
    
    pageName: string = "";
    reportsDataList: any;
    visitorReportData: any;
    totalItems: any;
    isDataLoaded: boolean = false;
    unitBlocksData: any;
    blockId = null;
    blockNo: string = "";
    end_date = null;
    start_date = null;
    visitByData: any;
    visitTypeId = null;
    listData: any;
    columnData:any;
    gridSourceData:any;
    reportData:any = "";
    
    @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
    @ViewChild('matDrawer', {static: true}) matDrawer: MatDrawer;
    
    filterData:any = {
        ticketStatus : '',
        staff : null,
        supervisor:null
    };

    filterSelected:string = "all";
    singleBlock:string = "Select Tower";
    selectedBlockId:number;

    timeZone:any;

    allLink: boolean = false;
    
    constructor(
        private route: ActivatedRoute, 
        private apartmentService: ApartmentService, 
        private visitorService: VisitorService,
        private lookupService: LookupService,
        private sharedService: SharedService, 
        private sessionService: SessionService) 
        { }
    
    
    getStartDate(event) {
      this.start_date = event
    }

    getEndDate(event) {
      this.end_date = event
    }

    isMobileView() {
        return window.innerWidth <= 767 ? 'table-responsive' : '';
    }

    getPrintParams(event){
        this.datagrid.exportdata(event, 'VisitorReportsData', true, null, true, 'https://www.jqwidgets.com/export_server/dataexport.php');
      }
    
    
    getDate(date) {
        return moment(date).add(this.timeZone.offset, 'hours').format(this.timeZone.time);
    }

    submitReportsFilterForm() {
        this.getDetails();
    }

    getDetails(){

        let params = {
          ApartmentID : this.sessionService.apartmentId,
          ApartmentBlockID : parseInt(this.blockId),
          StartDate : this.getDate(this.start_date),
          EndDate : this.getDate(this.end_date),
          VisitType : parseInt(this.visitTypeId)
        }

        let serviceName;
        let id = parseInt(this.route.params['value'].id);
        if(id == 365){
            serviceName = this.visitorService.getReportsForVisitorsCheckedInMultiFilter(params)
        }else if(id == 366){
            serviceName = this.visitorService.getReportsForVisitorsCheckedOutnMultiFilter(params)
        }else if(id == 367){
            serviceName = this.visitorService.getReportsForExpectedVisitorsMultiFilter(params)
        }else if(id == 368){
            serviceName = this.visitorService.getReportsForVisitorsNotCheckedOutMultiFilter(params)
        }else if(id == 369){
            serviceName = this.visitorService.getReportsForExpectedVisitorsNotCheckedInMultiFilter(params)
        }
        serviceName.subscribe((res:any) => {
            if(res.errorMessage){
              this.reportsDataList = [];

            }else{
            
            this.reportsDataList = res;

            this.gridSourceData = {
              localdata: this.reportsDataList,
              datatype: "array"
            }
        
            this.listData = new jqx.dataAdapter(this.gridSourceData);
            this.totalItems = this.reportsDataList.length;

            this.isDataLoaded = true;
          }},
          error => {
          this.isDataLoaded = true;
                });
    
    }

    getDateTime(val){
        if(val != ''){
          return moment(val).format("MM/DD/YY hh:mm")
        }else{
          return ''
        }
      }

      onGlSearchFilter(event) {
        if(event != ""){
      
          let filtergroup = new jqx.filter();
          let filter_or_operator = 1;
          let filtervalue = event;
          let filtercondition = 'contains';
          let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
          filtergroup.operator = 'or';
          filtergroup.addfilter(filter_or_operator, filterData);
        
          this.datagrid.showfiltercolumnbackground(false);
        
          this.columnData.forEach(item => {
            if(item.datafield != 'Actions') {
            this.datagrid.addfilter(item.datafield, filtergroup, true);
            }
          })
        
          this.datagrid.applyfilters();
          
          }
          else {
          this.datagrid.clearfilters();
          }
         }

      getSelectedBlock(event) {
        this.blockId = event[0].apartmentBlockId;
      }

      getSelectedVisit(event) {
        this.visitTypeId = event[0].lookupValueId;
      }
    
      filterApply() {
        this.goBack();
        this.isDataLoaded = false;
        this.getDetails();
        
      }
    
      clearFilter() {
     this.blockId = null;
     this.start_date = null;
     this.end_date = null;
     this.visitTypeId = null;
        this.getDetails();
        this.goBack();
      }
    
      goBack() {
        this.matDrawer.close();
      }
    
      cancel(form){
          form.reset();
     }

     getPageName(name) {
      return name = name.replace(/-/g, ' ');
     }
    
    ngOnInit() {

       this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

        this.pageName = this.route.params['value'].name;
        
        let unitBlockParams = {
            apartmentId: this.sessionService.apartmentId
        };
        this.apartmentService.getApartmentBlockByApartmentId(unitBlockParams).subscribe((res: any) => {
            this.unitBlocksData = res;
        });
        let visitParams = {
            ApartmentId: this.sessionService.apartmentId,
            LookupTypeId: 15,
        }

        //visit type
        this.lookupService.getLookupValueByLookupTypeId(visitParams).subscribe((res: any) => {
            this.visitByData = res;
        },
            error => {

            }
        );

        var cellsrenderer = (row, column, value) => {
            return '<div class="jqx-custom-inner-cell">' + value + '</div>';
          }
      
          var columnrenderer = (value) => {
            return '<div style="padding: 14px">' + value + '</div>';
          }
      
      
          this.columnData = [{
               text: 'Id',
               datafield: 'serialNo',
               width: 100,
               pinned: true,
               cellsrenderer: cellsrenderer,
               renderer: columnrenderer
            }, {
                text: 'Visitor Name',
                datafield: 'visitorName',
                width: 200,
                cellsrenderer: cellsrenderer,
                renderer: columnrenderer
            },{
               text: 'Phone Number',
               datafield: 'visitorPhone',
               cellsrenderer: cellsrenderer,
               minwidth: 150,
               renderer: columnrenderer
           }, {
               text: 'Date/Time of Visit',
               datafield: 'visitorInTime',
               cellsrenderer: (row, column, value) => {
                return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
              },
               minwidth: 170,
               renderer: columnrenderer
           }, {
               text: 'Time-out',
               datafield: 'visitorOutTime',
               cellsrenderer: (row, column, value) => {
                return '<div class="jqx-custom-inner-cell">'+ this.getDateTime(value) +'</div>'
              },
               minwidth: 170,
               renderer: columnrenderer
           }, {
               text: 'Visit Type',
               datafield: 'visitType',
               cellsrenderer: cellsrenderer,
               minwidth: 80,
               renderer: columnrenderer
           },{
            text: 'Tower No',
            datafield: 'blockNo',
            cellsrenderer: cellsrenderer,
            minwidth: 170,
            renderer: columnrenderer 
            }, {
                text: 'Unit No',
                datafield: 'unitNo',
                cellsrenderer: cellsrenderer,
                minwidth: 170,
                renderer: columnrenderer 
            }];
     
            this.getDetails();

    }

    getAllBlockData(){

		this.filterSelected = 'all';
	  this.singleBlock = "Select Tower";
	  
	  this.blockId = "";
	  this.datagrid.clearfilters();
  
	}

	getSingleBlock(block){
  	
		this.filterSelected = 'single';
		this.singleBlock = block.apartmentBlockNumber;
		this.blockId = block.apartmentBlockId;

		let filtergroup = new jqx.filter();
			let filter_or_operator = 1;
			let filtervalue = this.singleBlock;
			let filtercondition = 'contains';
			let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
			filtergroup.operator = 'or';
			filtergroup.addfilter(filter_or_operator, filterData);
	  
			this.datagrid.showfiltercolumnbackground(false);
	  
			this.columnData.forEach(item => {
			  if(item.datafield != 'Actions') {
				this.datagrid.addfilter(item.datafield, filtergroup, true);
			  }
			})
	  
			this.datagrid.applyfilters();
	}
}
